import React from "react";
import "./navbar.css";
import { Link } from "react-router-dom";
import logo from "./kgp.png";

function nav() {
  let onClickMenu = () => {
    document.getElementById("menu").classList.toggle("change");
    document.getElementById("nav").classList.toggle("change");
    document.getElementById("menu-bg").classList.toggle("change-bg");
  };
  return (
    <div>
      <div className="res">
        <div className="navbar_1 navbar_2">
          <a href="http://iitkgp.ac.in/" target="_blank">
            <img src={logo} className="navbar_logo" />
          </a>
          <Link to="/participant" className="navbar_text">
            Participants
          </Link>
          <Link to="/subject" className="navbar_text">
            Subject
          </Link>
          <Link to="/" className="navbar_text">
            Home
          </Link>
        </div>
      </div>
      <div className="res_1">
        <div id="menu-bar">
          <div id="menu" onClick={onClickMenu}>
            <div id="bar1" className="bar"></div>
            <div id="bar2" className="bar"></div>
            <div id="bar3" className="bar"></div>
          </div>
          <ul className="nav" id="nav">
            <li>
              <Link to="/" onClick={onClickMenu}>
                Home
              </Link>
            </li>
            <li>
              <Link to="/subject" onClick={onClickMenu}>
                Subject
              </Link>
            </li>
            <li>
              <Link to="/participant" onClick={onClickMenu}>
                Participants
              </Link>
            </li>
          </ul>
        </div>
        <div class="menu-bg" id="menu-bg"></div>
      </div>
    </div>
  );
}
export default nav;
