import React from "react";
import "./participant.css";
function Participant() {
  let pop_up = () => {
    if (i == 6) {
      alert("No participant");
    } else {
      document.querySelector(".popup_hidden").style.display = "block";
    }
  };
  let pop_up_close = () => {
    document.querySelector(".popup_hidden").style.display = "none";
  };
  var i = 0;
  let delete_card = (name) => {
    document.querySelector(name).style.display = "none";
    i = i + 1;
    alert("Deleted");
  };
  return (
    <div>
      <div className="part">
        <div className="row_1">
          <div className="column_1 ">
            <div className="card_1 card_3">
              <span className="data">Rabri Jhopra</span>
              <span className="data">1XCS10X</span>
              <span className="data">
                <button
                  className="button"
                  id="bu_1"
                  onClick={() => delete_card(".card_3")}
                >
                  Delete
                </button>
              </span>
            </div>
          </div>
          <div className="column_1 ">
            <div className="card_1 card_4">
              <span className="data">Jamkuri Devi</span>
              <span className="data">1XEC10X</span>
              <span className="data">
                <button
                  className="button"
                  id="bu_1"
                  onClick={() => delete_card(".card_4")}
                >
                  Delete
                </button>
              </span>
            </div>
          </div>
          <div class="column_1">
            <div class="card_1 card_5">
              <span className="data">Tagari Ram</span>
              <span className="data">1XNA10X</span>
              <span className="data">
                <button
                  className="button"
                  id="bu_1"
                  onClick={() => delete_card(".card_5")}
                >
                  Delete
                </button>
              </span>
            </div>
          </div>
          <div class="column_1 ">
            <div class="card_1 card_6">
              <span className="data">Harku Devi</span>
              <span className="data">1XMT10X</span>
              <span className="data">
                <button
                  className="button"
                  id="bu_1"
                  onClick={() => delete_card(".card_6")}
                >
                  Delete
                </button>
              </span>
            </div>
          </div>
          <div class="column_1">
            <div class="card_1 card_7">
              <span className="data">Patanjali Kumrawat </span>
              <span className="data">1XCY10X</span>
              <span className="data">
                <button
                  className="button"
                  id="bu_1"
                  onClick={() => delete_card(".card_7")}
                >
                  Delete
                </button>
              </span>
            </div>
          </div>
          <div class="column_1 ">
            <div class="card_1 card_8">
              <span className="data">Puchri Roy</span>
              <span className="data">1XCE10X</span>
              <span className="data">
                <button
                  className="button"
                  id="bu_1"
                  onClick={() => delete_card(".card_8")}
                >
                  Delete
                </button>
              </span>
            </div>
          </div>
        </div>
        <button className="button" id="add_button" onClick={pop_up}>
          AddAll
        </button>
      </div>

      <div className="popup popup_hidden">
        <div id="content">
          <div id="cancel" onClick={pop_up_close}>
            X
          </div>
          <div id="context">Participant Added</div>
          <button className="button" id="but_3" onClick={pop_up_close}>
            Ok
          </button>
        </div>
      </div>
    </div>
  );
}

export default Participant;
